<!-- 个人福利 -->
<template>
  <van-nav-bar left-arrow fixed title="个人福利" @click-left="handleBack">
    <template #right>
      <img src="../assets/images/kf.png" @click="openLayer = true" />
    </template>
  </van-nav-bar>
  <div class="container">
    <div class="main-content">
      <div class="welfare-item" v-for="(item, index) in datalist" :key="index">
        <label @click="viewIntroduce(item)">{{ item.type }}
          <van-icon name="warning" size="16" color="#dfad21" />
        </label>
        <div class="res-text" v-html="item.valueText"></div>
        <van-button :type="item.disable === 0 && item.reward > 0 ? 'primary' : 'default'" @click="handleReceive(item)">
          {{ item.disable === 1 ? '已领取' : '领取奖励' }}</van-button>
      </div>
    </div>
  </div>
  <van-popup v-model:show="showPopup" round closeable>
    <div class="activity-cont">
      <div class="title">{{ dataItem.title }}</div>
      <div class="scroll-cont">
        <div class="subtitle">活动说明：</div>
        <div class="cus-list" :class="dataItem.align">
          <div class="cus-item" v-for="(it, idx) in dataItem.child" :key="idx" v-html="it"></div>
        </div>
      </div>
    </div>
  </van-popup>
  <van-popup round v-model:show="showLogin">
    <login @login="login" />
  </van-popup>
  <van-popup v-model:show="openLayer" closeable round>
    <service />
  </van-popup>
</template>

<script>
import { welfareList } from "../utils/resources"
import Login from "./Login.vue";
import Service from "../components/Service";
export default {
  name: 'Benefits',
  components: { Login, Service },
  data() {
    return {
      datalist: [],
      showLogin: false,
      showPopup: false,
      dataItem: null,
      openLayer: false
    }
  },
  created() {
    this.getFuliList();
  },
  methods: {
    //获取福利
    getFuliList() {
      this.$toast.loading({ message: '加载中...', duration: 0, overlay: true })
      this.get("get_userbonus").then(res => {
        this.$toast.clear();
        let list = res.data || [];
        for (let i = 0; i < list.length; i++) {
          let { type, num, disable } = list[i];
          if (type === '连续签到3天') {
            if (num > 3 && disable === 1) {
              list.splice(i, 1);
              break;
            }
          }
          if (type === '连续签到7天') {
            if (num <= 3 || list.find(it => it.type === '连续签到3天' && it.disable != 1)) {
              list.splice(i, 1);
              break;
            }
          }
        }
        list.forEach(it => {
          if (it.type === '连续签到7天' || it.type === '连续签到3天') {
            let { child, valueText } = welfareList[5];
            it.child = child;
            it.valueText = valueText.replace("***", `<span>${it.num || 0}</span>`)
          } else {
            welfareList.find(({ title, child, valueText }) => {
              if (title.indexOf(it.type) >= 0) {
                it.child = child;
                it.valueText = valueText.replace("***", `<span>${it.num || 0}</span>`)
                it.valueText = it.valueText.replace("xxx", `<span>${it.reward || 0}</span>`)
                return true
              }
              return false;
            });
          }
        })
        this.datalist = list;
      })
    },
    handleBack() {
      this.$router.back();
    },
    //领取奖励
    handleReceive(item) {
      if (item.disable === 1) return
      if (item.disable < 0 || item.reward <= 0) return
      this.$toast.loading({ message: '奖励领取中...', duration: 0, overlay: true });
      this.post("set_userbonus", {
        type: item.type,
        token: item.token,
        num: item.reward
      }).then(res => {
        this.$toast("领取成功")
        item.disable = 1;
        console.log(res);
      })
    },
    viewIntroduce(item) {
      this.dataItem = item;
      this.showPopup = true;
    },
  }
}
</script>
<style lang='scss' scoped>
.welfare-item {
  display: flex;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 10px;
  background-color: #fff;
  align-items: center;
  justify-content: space-between;

  label {
    width: 100px;
    font-weight: bold;
    position: relative;

    .van-badge__wrapper {
      position: absolute;
      top: -5px;
    }
  }

  .res-text {
    flex: 1 1 0;
    color: #999;
    margin-right: 10px;
    text-align: right;

    :deep(span) {
      padding: 0 3px;
      color: var(--main-color);
      font-weight: bold;
    }
  }

  .van-button {
    height: 26px;
    padding: 0 10px;
    border-radius: 30px;

    :deep(.van-button__content) {
      font-size: 10px;
    }
  }
}

.activity-cont {
  padding: 10px;
  width: 280px;
  box-sizing: border-box;

  .title {
    color: var(--main-color);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }

  .scroll-cont {
    max-height: 400px;
    overflow-y: auto;

    .subtitle {
      color: var(--main-color);
      font-size: 14px;
      font-weight: bold;
    }

    .cus-list {
      padding-bottom: 10px;

      .cus-item {
        font-size: 12px;
        letter-spacing: 0.5px;
        margin-top: 10px;

        :deep(span) {
          color: #ff4444;
        }
      }

      &.center {
        text-align: center;
      }
    }
  }
}
</style>